import React from "react"
import PropTypes from "prop-types"
import { Link } from "gatsby"
import { asTagPath } from "../utils/tags"

const Item = ({ excerpt, path, date, title, tags }) => (
  <li className="post-item">
    <Link to={path} title={excerpt}>
      <h1>{title}</h1>
    </Link>
    <div>
      <h2 className="post-item-date">{date}</h2>
      {tags.map(tag => (
        <div key={`${path}_${tag}`} className="chip">
          <Link to={`/tags/${asTagPath(tag)}`}>{tag}</Link>
        </div>
      ))}
    </div>
    <p>{excerpt}</p>
  </li>
)

Item.propTypes = {
  excerpt: PropTypes.string.isRequired,
  path: PropTypes.string.isRequired,
  date: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  tags: PropTypes.arrayOf(PropTypes.string).isRequired,
}

export default Item
